
#iconContainer 
{
    color: #DFB400;
    z-index: 90;
    display: flex;
    justify-content: center;
}

#iconContainer img
{  
      width: 6% !important;
      height: 0.05% !important;
      margin-top: -7.5%;
      z-index: 90;
}


@keyframes jump { 0% { top: 0; } 50% { top: 10px; } 100% { top: 0; } }


#iconContainer 
{ 
  position: relative; 
  margin: 0 auto; 
  text-align: center;
  animation: jump 1.5s infinite;
}

img:hover
{ 
  color: #FFAAAA; 
}


h3, h2, h6 {

	text-align:center;
	font-weight: bold;
  font-family: 'Exo', sans-serif;
}


#toppick
{
  text-align:initial;
  padding-left: 3.2%;
  padding-top: 1%;
  font-size: 20px;
  font-family: 'Oswald', sans-serif;
}

#homeline {
    width: 200px;
    height: 1px;
    margin-left: 3.2%;
    display: block;
    background-color: grey;
    margin-bottom:1%;
    opacity: 0.3;
}

#line {
  width: 55px;
    height: 2px;
    margin: 8px auto 0;
    display: block;
    background-color: grey;
    margin-bottom:5%;
    opacity: 0.5;
}

#wrapper
{
      margin: 0 auto;
      padding-top: 5%;
      padding-bottom: 8%;
}

.wrapper2
{
      margin: 0 auto;
      padding-top: 10%;
      padding-bottom: 15%;
}

/* Market Page*/
#market-btn{
    display:flex;
    flex:row;
}

#market-head {
    margin-top:9%;
}

#market-line {
    width: 55px;
    height: 2px;
    margin: 8px auto 0;
    display: block;
    background-color: grey;
    margin-bottom:3%;
    opacity: 0.5;
}
#market-line-market {
    width: 55px;
    height: 2px;
    margin: 0px auto 0;
    display: block;
    background-color: grey;
    margin-bottom:5%;
    opacity: 0.5;
}
#market-list {
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    justify-content:space-between;
    padding-top:7%;
    padding-left:15%;
    padding-right:18%;
}

@media only screen and (max-width: 640px) {
    
    
    #market-list {
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        justify-content:space-between;
        padding-top:7%;
        padding-left:15%;
        padding-right:18%;
    }

    #market-btn{
       all:unset !important;
    }
}


/* Search Page */
